:root {
  --background: #020202;
  --foreground: #f6f6fd;
  --accent: #696aac;
  --primary: #3e3f7e;
  --secondary: #a2a3e9;
  --tertiary: #c7c8f2;
  --lightaccent: #e3e3f8;
  --w-main: 1500px;
}
html {
  scroll-behavior: smooth !important;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}
.bg-gradient-1 {
  background: linear-gradient(
    to bottom,
    rgba(2, 2, 2, 0.9) 1%,
    rgba(2, 2, 2, 0.6) 30%,
    rgba(2, 2, 2, 0.6) 40%,
    rgba(2, 2, 2, 1) 100%
  );
}
@media screen and (max-width: 1536px) {
  :root {
    --w-main: calc(100% - 60px);
  }
}
@media screen and (max-width: 640px) {
  :root {
    --w-main: calc(100% - 40px);
  }
}
